import React, { useState } from 'react'
import { Link } from 'react-router-dom'

const Home = () => {
    useState(() => {
        document.title = "MCWIKI | Trang chủ"
    }, [])

    return (
        <div className="container">
            <div className="tab">
                <div>
                    <p>- Chào mừng bạn đến với Minecraft Wiki được tạo bởi một player Tên: MineHighVN (Java Edition) NNCGamer (Bedrock Edition)</p>
                    <p>- Đây là một sản phẩm kế nhiệm từ sản phẩm <a href="https://minehighvn.github.io/commandmc">CommandMC</a> cũng là sản phẩm được làm bởi mình nhưng đã bị bỏ dở</p>
                    <p>- Đây là mơi mà bạn có thể tra gần như tất cả mọi thứ về Minecraft bằng ngôn ngữ tiếng Việt</p>
                    <p>- Mình không tạo ra Wiki mà các bạn tạo ra wiki, mình chỉ có vai trò tạo ra giao diện vào kiểm duyệt nội dung cho website, còn lại các bạn có thể gửi bất kì một bản wiki nào đó cho mình qua facebook của mình.</p>
                </div>
                <div>
                    <p>- Chi tiết cách tạo ra một bài viết: <Link to="/howtocreate">TẠI ĐÂY</Link></p>
                </div>
                <div>
                    <h2 style={{fontWeight: "500"}}>HƯỚNG DẪN CƠ BẢN</h2><br/>
                    <p>Trong khi bạn viết command, bạn sẽ thấy những thứ ví dụ như sau: /gamemode &lt;gameMode: int&gt; [player: target] bạn đang tự hỏi nó là gì đúng không? sau đây mình sẽ hướng dẫn chi tiết về nó</p>
                    <div className="container">
                        <p>&lt;tên_biến: kiểu_dữ_liệu&gt; như vậy là ta đã hiểu &lt;gameMode: int&gt; là gì rồi, gameMode là tên_biến, int là kiểu_dữ_liệu, nếu bạn giỏi tiếng Anh thì bạn có thể đọc tên biến để tự suy ra giá trị mà không cần đọc wiki, và một điều CẦN LƯU Ý: khi sử dụng &lt;tên_biến: kiểu_dữ_liệu&gt; tức là sử dụng &lt;...&gt; hoặc nói chính xác hơn là có dấu lớn và dấu bé bao quanh thì trường này bắt buộc phải ghi một giá trị</p>
                        <p>[tên_biến: kiểu_dữ_liệu] cũng tương tự như trên thôi, player là tên_biến, target là kiểu_dữ_liệu, nhưng khác biệt ở chỗ là [tên_biến: kiểu_dữ_liệu] tức là [...] hoặc nói chính xác hơn là có ngoặc vuông bao quanh thì không cần phải ghi, nếu không ghi thì nó sẽ áp dụng giá trị mặc định</p>
                    </div>
                    <div className="container">
                        <p>KIỂU DỮ LIỆU CƠ BẢN:</p>
                        <div className="container">
                            <p>+ int: Đây là kiểu số nguyên, có thể lưu trữ được các số (1,2,3,4,5...)</p>
                            <p>+ string: Đây là kiểu chuỗi, kiểu này sử dụng để nhập các kí tự</p>
                            <p>+ char: đây là kiểu kí tự, kiểu này chỉ có thể lưu trữ được duy nhất một kí tự trên bàn phím</p>
                            <p>+ float: đây là kiểu số thực, kiểu này có thể nhập được các giá trị số thực (1.1, 1.2, 1.3), Lưu ý: 1.1 là 1,1 vì máy tính không hiểu 1,1 là để ngăn cách hay để chỉ dấu phảy nên chúng ta sử dụng dấu chấm để thay cho dấy phảy</p>
                            <p>Trên đây chỉ là những kiểu dữ liệu cơ bản, những kiểu dữ liệu khác chưa có sẽ xuất hiện trong wiki.</p>
                        </div>
                    </div>
                    <div className='container'>
                        <p>Những kiểu dữ liệu trong Minecraft</p>
                        <div className='container'>
                            <p>+ target: những sinh vật trong minecraft, về target thì có những cách viết sau: @e là để chỉ tất cả sinh vật trong một khoảng chunk nhất định, @p là người chơi gần nhất tính từ điểm thực hiện command (hoặc người chơi nếu sử dụng /execute at), @s là người sử dụng command, @r là người chơi bất kì, @a là tất cả người chơi trong thế giới hoặc có thể ghi trực tiếp tên người chơi ra (không áp dụng cho sinh vật), ví dự sử dụng: /tp @s MineHighVN, trong target còn có phân bổ sung (không cần thiết) hay nói cách khác là lọc ra người chơi nhất định, cụ thế như sau:</p>
                            <div className="container">
                                <p>- x: Thực hiện command ở tọa độ x thay vì nơi sử dụng command vd: @e[x=35]</p>
                                <p>- y: Thực hiện command ở tọa độ y thay vì nơi sử dụng command vd: @p[y=-5]</p>
                                <p>- z: Thực hiện command ở tọa độ z thay vì nơi sử dụng command vd: @e[z=~2]</p>
                                <p>- tag: ai có một tag nào đó này thì sẽ thực hiện vd: @e[tag=jail]</p>
                                <p>- name: tên của sinh vật, có thể áp dụng cho người chơi vd: @a[name=NNCGamer]</p>
                                <p>- type: sinh vật có tên mà minecraft đặt cho là gì, ví dụ người chơi là player, zombie là zombie, dân làng là villager vd: @e[type=player]</p>
                                <p>- r: bán kính tính từ vị trí thực hiện command, có thể kết hợp với x,y,z, vd: @a[x=3,y=-60,z=5,r=2] thì tất cả người chơi nằm ở tọa độ x=3, y=-60, z=5 và nằm trong bán kính 2 block thì sẽ được chọn</p>
                                {/* <p>- hasitem: hasitem là kiểm tra xem người chơi có vật phẩm nào đó trong người không syntax (do mình tự định nghĩa cho dễ đọc) hasitem=[{type=value}], vd: @a[hasitem=[{item=diamond_sword}]], trong đó type bao gồm: data: data của vật phẩm, trong một số trường hợp data dùng để chỉ các đồ vật có tên giống nhau nhưng màu khác nhau hoặc bền,... location: kiểm tra đồ vật tại vị trí của kho đồ người chơi, vd: location=slot.weapon.mainhand, item: tên của vật phẩm</p> */}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Home