import React, { useMemo, useState } from 'react'
import css from "../css/settings.module.css"
import cookies from "cookie-universal"

const Settings = () => {
    const Cookies = useMemo(() => new cookies(), [])

    const [ viewType, setViewType ] = useState(Cookies.get("viewType"))

    const changeViewType = (e) => {
        setViewType(e.target.value)
        Cookies.set("viewType", e.target.value, { maxAge: Math.floor(Date.now()/1000+60*60*24*365) })
    }

    return (
        <div className={`container ${css.settings}`}>
            <div>Chọn kiểu hiển thị của bạn: <select onChange={changeViewType} value={viewType === "ng" ? "ng" : "lb"}>
                <option value="lb">Hiển thị kiểu lá bài</option>
                <option value="ng">Hiển thị kiểu nằm ngang</option>
            </select></div>
            <div>Chọn kiểu hiển thị là nơi để chọn cách mà các nút bấm trong phần wiki hiển thị</div>
        </div>
    )
}

export default Settings